
import { Pipe,PipeTransform } from '@angular/core';
import { tarjetas } from '../data/datos';
import { Cards } from '../model/cards';

@Pipe({
  name: 'cardFilter'
})


export class CardFilterPipe implements PipeTransform {
 cards:Cards

  transform(cards: Cards[], searchTerm: string): Cards[] {

    if (!searchTerm) {
      return cards; // Si searchTerm no est definido, retorna el arreglo de cards sin modificar
    }

    if (!cards || !searchTerm) {
      return cards; // Si no hay tarjetas o trmino de bsqueda, retorna todas las tarjetas
    }

    searchTerm = searchTerm.toLowerCase(); // Convierte searchTerm a minsculas

    return cards = cards.filter(target=>target.id>=1); // Convierte el ttulo de la carta a minsculas
      // Devuelve true si el ttulo incluye searchTerm
    };
  }



